.App {
  background: linear-gradient(to right, #4b2a93,#743b97 ,#661a6b,#3a0a3a );
  background-size: 100% 100% ;
  text-align: center;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;  
}

.App-todo {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
}




.box{
  margin-top: 30px;
  width: 430px;
  height: 600px; 
  background-color: #393f46;
  border-radius: 30px;
  box-shadow: 25px 25px 75px  rgba(0, 0, 0, 0.25),
  inset 5px 5px 10px rgba(0, 0, 0, 0.5),
  inset 5px 5px 20px rgba(78, 75, 75, 0.271),
  inset -5px -5px 15px rgba(0, 0, 0, 0.915);
  overflow-x: hidden;
  
}
@media screen and (max-width: 768px) {
  .box{
    width: 95vw;
    height: 100vh;
  }
}

@media screen and (max-width: 768px) {
  .modal{
    width: 55vw;
    height: 100vh;
  }
  
}

.title{
  letter-spacing: 10px;
  font-family:m;
  color: rgb(0, 0, 0); 
  padding:  20px;
  text-align: center;
  filter: drop-shadow(0 0 10px rgb(255, 255, 255));
  
}


#input:focus-visible{
  text-align: start;
  box-shadow: 5px 5px 7px  rgba(211, 206, 206, 0.25),
  inset 2px 2px 5px rgba(0, 0, 0, 0.5),
  inset -3px -3px 5px rgba(0,0,0,0.5);
  text-transform:lowercase;
}
#input{
  color: rgb(35, 10, 6);
  text-align: center;
  width: 80%;
  height: 50px;
  border-radius: 15px;
  border: none;
  outline: none;
  font-size: 20px;
  box-shadow: 5px 5px 7px  rgba(0, 0, 0, 0.25),
  inset 2px 2px 5px rgba(0, 0, 0, 0.5),
  inset -3px -3px 5px rgba(0,0,0,0.5);
  
}

li{
  margin-bottom: 10px;
  text-align: start;
  padding-left: 6px;
  padding-top: 12px;
  position: relative;
  color: white;
  list-style: none;
  width: 70%;
  height: 40px;
  border-radius: 15px;
  font-size: 18px;
  box-shadow: 5px 5px 7px  rgba(0, 0, 0, 0.693),
  inset 2px 2px 5px rgba(207, 206, 218, 0.604),
  inset -3px -3px 5px rgba(135, 131, 182, 0.679);
  
}

li i {
  text-align: center;
  padding-top: 5px;
  position: absolute;
  top:15%;
  left:  124%;
  width: 25px;
  height: 30px;
  border-radius: 30%;
  background-color:rgb(94, 22, 22) ;
  border: 2px solid rgb(77, 14, 26);
  box-shadow: 5px 5px 7px  rgba(0, 0, 0, 0.25),
  inset 2px 2px 5px rgba(0, 0, 0, 0.5),
  inset -3px -3px 5px rgba(0, 0, 0, 0.678);
  transition: all 0.5s;
  
}
li span {
  text-align: center;
  padding-top: 5px;
  position: absolute;
  top:15%;
  left:  104%;
  width: 36px;
  height: 30px;
  border-radius: 20%;
  background-color: rgb(187, 164, 59);
  border: 2px solid rgb(77, 61, 14);
  box-shadow: 5px 5px 7px  rgba(0, 0, 0, 0.25),
  inset 2px 2px 5px rgba(0, 0, 0, 0.5),
  inset -3px -3px 5px rgba(0, 0, 0, 0.678);
  transition: all 0.5s;
}

#wrapper{
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.modal {
  top: 40%;
  border-radius: 10px;
  background-color: #ccccd4;
  width: 350px;
  height: 20%;
  position: absolute;
  
}


.close{
  color: white;
  padding-top: 5px;
  position: absolute;
  top:15%;
  left:  84%;
  width: 45px;
  height: 35px;
  border-radius: 20%;
  background-color: rgb(164, 42, 67);
  border: 2px solid rgb(77, 14, 26);
  box-shadow: 5px 5px 7px  rgba(0, 0, 0, 0.25),
  inset 2px 2px 5px rgba(0, 0, 0, 0.5),
  inset -3px -3px 5px rgba(0, 0, 0, 0.678);
  transition: all 0.5s;
}
li :hover{
  cursor: pointer;
  background-color: rgb(149, 23, 42);}

li span:hover{
  background-color: rgb(221, 202, 59);}

 